/* 0. Reset ================================= */
@import 'reset';
@import 'base/media';
@import 'base/colors';
@import 'base/fonts';
@import 'base/_mixins';
@import 'base/_about';
@import 'base/_gallery';


/* 1. Main Layout ================================= */

* {
	box-sizing: border-box;
}

html{
	width:100%;
	
}

body {

	min-width:350px;
	background: $gridRed;
	background-image: url("/dist/img/circle_g.png");
	background-repeat: no-repeat;
	background-size: 150%;
	background-position-x:-55%; 
	background-position-y:-15%; 
	@include mainText();

	@media#{$tablet}{
		background-size: 95%;
		background-position-x:600%; 
	}

}

